<template>
  <div id="user_add">
    <Modal
      v-model="build"
      title=""
      class-name="vertical-center-modal"
    >
      <Form ref="add" :model="AddData" :rules="AddRule" :label-width="90" style="padding:0 50px;">
        <FormItem prop="version" label="">
          <Input v-model.trim="AddData.version" placeholder="" size="default"/>
        </FormItem>
        <FormItem prop="type" label="">
          <Select v-model="type" style="width:100%" size="default">
            <Option value="1"></Option>
            <Option value="2"></Option>
            <Option value="3"></Option>
            <Option value="4"></Option>
          </Select>
        </FormItem>
        <FormItem label="zip"  prop="file">
          <Upload
            v-model="AddData.file"
            :before-upload="handleUpload"
            :accept="'.shp,.zip'"
            :format="['.shp','.zip']"
            :max-size=102400
            action="#"
            ref="upload"
          >
            <Button size="small"></Button>
          </Upload>
          <span style="margin-left: 10px">
              :
              <span v-if="AddData.file === null"></span>
              <span v-if="AddData.file !== null">{{ AddData.file.name }}</span>
            </span>
        </FormItem>
        <FormItem prop="addRealName" label="">
          <Input v-model.trim="AddData.remark" placeholder="" size="default"/>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="verification" :loading="loadingStatus"></Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { shapeVersionAdd } from '@/api/systemManage'
export default {
  name: 'VersionShapeAdd',
  data () {
    //   fileList , 
    const validateUpload = (rule, value, callback) => {
      if (this.AddData.file === null) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    return {
      /*  */
      AddData: {
        version: '',
        remark: '',
        file: null
      },
      type: '1',
      AddRule: {
        version: [
          { required: true, message: '', trigger: 'blur' },
          { pattern: /^([1-9]\d*|0)(\.\d{1,2})?$/, message: '1.0', trigger: 'blur' }
        ],
        file: [
          { required: true, validator: validateUpload, trigger: 'change' }
        ]
      },
      /*  */
      build: false,
      loadingStatus: false
    }
  },
  mounted () {
    this.init()
  },
  methods: {
    /* excal */
    handleUpload (file) {
      this.AddData.file = file
      this.$refs['add'].validate(() => {})
    },
    /*  */
    upload () {
      let param = new FormData()
      param.append('file', this.AddData.file)
      param.append('type', this.type)
      param.append('version', this.AddData.version)
      param.append('remark', this.AddData.remark)
      param.append('createBy', this.$store.getters.getloginInfor().userId)
      this.params = param
      shapeVersionAdd(this.params).then((res) => {
        console.log(res)
        if (res.data.code === '0000') {
          this.loadingStatus = false
          this.$Notice.success({ title: '' })
          this.$emit('getNewData')
          this.build = false
          this.init()
        } else {
          this.loadingStatus = false
          this.$Notice.error({ title: res.data.msg })
        }
      })
    },
    /*  */
    verification () {
      this.loadingStatus = true
      this.$refs['add'].validate((valid) => {
        if (valid) {
          this.upload()
        } else {
          this.loadingStatus = false
        }
      })
    },
    /*  */
    init () {
      this.AddData = {
        version: '',
        remark: '',
        file: null
      }
      this.$refs.upload.clearFiles()
    }
  }
}
</script>

<style scoped lang="scss">
</style>
<style lang="scss">
  .vertical-center-modal{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: left;
    .ivu-modal{
      top: 0;
    }
  }
</style>
